"use client";

import { useState } from "react";
import { IconHeart, IconHeartFilled, IconX } from "@tabler/icons-react";
import styles from "../playlist.module.css";

interface Song {
  id: number;
  title: string;
  artist: string;
  progress: number;
  liked: boolean;
}

const PLAYLIST_SONGS: Song[] = [
  {
    id: 1,
    title: "Midnight City",
    artist: "The Weeknd",
    progress: 65,
    liked: false,
  },
  {
    id: 2,
    title: "Starboy",
    artist: "The Weeknd",
    progress: 45,
    liked: true,
  },
  {
    id: 3,
    title: "Blinding Lights",
    artist: "The Weeknd",
    progress: 80,
    liked: false,
  },
  { id: 4, title: "After Hours", artist: "The Weeknd", progress: 30, liked: true },
  {
    id: 5,
    title: "Save Your Tears",
    artist: "The Weeknd",
    progress: 55,
    liked: false,
  },
  {
    id: 6,
    title: "Earned It",
    artist: "The Weeknd",
    progress: 70,
    liked: true,
  },
  {
    id: 7,
    title: "Can't Feel My Face",
    artist: "The Weeknd",
    progress: 40,
    liked: false,
  },
  {
    id: 8,
    title: "Often",
    artist: "The Weeknd",
    progress: 85,
    liked: false,
  },
];

const PlaylistSongList = () => {
  const [songs, setSongs] = useState<Song[]>(PLAYLIST_SONGS);

  const toggleLike = (id: number) => {
    setSongs(
      songs.map((song) =>
        song.id === id ? { ...song, liked: !song.liked } : song
      )
    );
  };

  const removeSong = (id: number) => {
    setSongs(songs.filter((song) => song.id !== id));
  };

  return (
    <div className={styles.songListContainer}>
      {/* List Header */}
      <div className={styles.songListHeader}>
        <div>#</div>
        <div>Title</div>
        <div>Progress</div>
        <div>Duration</div>
        <div>Actions</div>
      </div>

      {/* Song List */}
      <div className={styles.songList}>
        {songs.map((song, index) => (
          <div key={song.id} className={styles.songItem}>
            {/* Song Number */}
            <div className={styles.songNumber}>{index + 1}</div>

            {/* Song Title & Artist */}
            <div>
              <div className={styles.songTitle}>{song.title}</div>
              <div className={styles.songArtist}>{song.artist}</div>
            </div>

            {/* Progress Bar */}
            <div className={styles.progressBar}>
              <div
                className={styles.progressFill}
                style={{ width: `${song.progress}%` }}
              />
            </div>

            {/* Duration */}
            <div className={styles.songArtist}>3:45</div>

            {/* Actions */}
            <div className={styles.songActions}>
              <button
                className={`${styles.actionButton} ${
                  song.liked ? styles.favoriteButton : ""
                }`}
                onClick={() => toggleLike(song.id)}
                title={song.liked ? "Remove from favorites" : "Add to favorites"}
              >
                {song.liked ? (
                  <IconHeartFilled size={18} />
                ) : (
                  <IconHeart size={18} />
                )}
              </button>
              <button
                className={`${styles.actionButton} ${styles.removeButton}`}
                onClick={() => removeSong(song.id)}
                title="Remove from playlist"
              >
                <IconX size={18} />
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Actions Footer */}
      <div className={styles.playlistActionsContainer}>
        <button className={`${styles.actionBtn} ${styles.updateBtn}`}>
          Update Playlist
        </button>
        <button className={`${styles.actionBtn} ${styles.deleteBtn}`}>
          Delete
        </button>
      </div>
    </div>
  );
};

export default PlaylistSongList;
